@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
@import url(https://fonts.googleapis.com/css?family=MonteCarlo);

.container{
    display: flex;
    padding: 20px 10%;
    
    
}
.brand{
    flex:1;
    font-size: 40px;
    font-weight: 900;
    color: white;
    font-family: 'MonteCarlo', cursive;
    

}
.menu{
  flex: 1;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}
a{
    text-decoration: none;
    color: white;
    font-size: large;
    margin-right: 20px;
    font-family: 'Roboto Mono', monospace;

}
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  .brand {
    font-size: 30px;
    margin-bottom: 10px;
  }

  .menu {
    flex-direction: row; /* <-- Î‘Î»Î»Î¬Î¶Î¿Ï…Î¼Îµ ÏƒÎµ row */
    justify-content: center; /* ÎšÎµÎ½Ï„ÏÎ¬ÏÎ¹ÏƒÎ¼Î± Î¿ÏÎ¹Î¶ÏŒÎ½Ï„Î¹Î± */
    align-items: center;
    gap: 15px; /* Î‘Ï€ÏŒÏƒÏ„Î±ÏƒÎ· Î¼ÎµÏ„Î±Î¾Ï Ï„Ï‰Î½ links */
  }

