/* Section spacing: Ï€ÏŒÏƒÎ¿ Î±Ï€Î­Ï‡ÎµÎ¹ Î±Ï€ÏŒ Ï„Î¿ banner Ï€Î¬Î½Ï‰ ÎºÎ±Î¹ Î±Ï€ÏŒ Ï„Î± Skills ÎºÎ¬Ï„Ï‰ */
.projects-section {
    

  width: 100%;
  max-width: 1200px;
  margin: 48px auto 56px;   /* â†‘ top gap | â†“ bottom gap Ï€ÏÎ¹Î½ Ï„Î± Skills */
  
}

.projects-section h2 {
  font-size: 26px;
  color: white;
  margin-bottom: 24px;
}

/* === Î‘Ï€Î»ÏŽÎ½Î¿Ï…Î¼Îµ Ï„Î¹Ï‚ ÎºÎ¬ÏÏ„ÎµÏ‚ Î¼Îµ Grid === */
.pj {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 24px;
}

/* Î— ÎºÎ¬ÏÏ„Î± Î³ÎµÎ¼Î¯Î¶ÎµÎ¹ Ï„Î¿ Î´Î¹Î±Î¸Î­ÏƒÎ¹Î¼Î¿ Ï€Î»Î¬Ï„Î¿Ï‚ Ï„Î·Ï‚ ÏƒÏ„Î®Î»Î·Ï‚ */
.projects-card {
  /* Î±Ï†Î±Î¹ÏÎ¿ÏÎ¼Îµ Ï„Î± flex/max-width ÏŽÏƒÏ„Îµ Î½Î± Î±Ï€Î»ÏŽÎ½ÎµÏ„Î±Î¹ */
  background: #2a2a2a;
  border-radius: 10px;
  padding: 2px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,.3);
  transition: transform .3s ease;
  height: 100%;
}

.projects-card:hover { transform: translateY(-5px); }

.projects-card img {
  width: 100%;
  height: 200px;              /* ÎºÏÎ±Ï„Î¬ Ï„Î¿ Î¯Î´Î¹Î¿ ÏÏˆÎ¿Ï‚ ÏŒÏ€Ï‰Ï‚ Ï†Î±Î¯Î½ÎµÏ„Î±Î¹ Ï„ÏŽÏÎ± */
  object-fit: contain;        /* âœ… Î´ÎµÎ¯Ï‡Î½ÎµÎ¹ ÎŸÎ›Î— Ï„Î·Î½ ÎµÎ¹ÎºÏŒÎ½Î± Ï‡Ï‰ÏÎ¯Ï‚ Î½Î± ÎºÏŒÎ²ÎµÏ„Î±Î¹ */
  background-color: #1a1a1a;  /* Ï€ÏÎ¿Î±Î¹ÏÎµÏ„Î¹ÎºÎ¬: Î´Î¹Î±ÎºÏÎ¹Ï„Î¹ÎºÏŒ Ï†ÏŒÎ½Ï„Î¿ Î³ÏÏÏ‰ Î±Ï€ÏŒ Ï„Î·Î½ ÎµÎ¹ÎºÏŒÎ½Î± */
  border-radius: 8px;
  display: block;
  margin: 0 auto;
}

.projects-card h3 { margin-top: 12px; font-size: 18px; color: #fff; }
.projects-card p  { margin-top: 6px;  font-size: 14px; color: #ddd; }

/* Breakpoints (Ï€ÏÎ¿Î±Î¹ÏÎµÏ„Î¹ÎºÎ¬ ÎµÎ»Î±Ï†ÏÏ fine-tune) */
@media (max-width: 600px) {
  .projects-section {
    padding: 0 12px;
  }
  .projects-section h2{
    text-align: center;
  }

  .pj {
    grid-template-columns: 1fr; /* âœ… Î¼Î¯Î± ÎºÎ¬ÏÏ„Î± Î±Î½Î¬ ÏƒÎµÎ¹ÏÎ¬ */
    justify-items: center;      /* âœ… ÎºÎµÎ½Ï„ÏÎ±ÏÎ¹ÏƒÎ¼Î­Î½Î· ÏƒÎµ Î¼Î¹ÎºÏÎ® Î¿Î¸ÏŒÎ½Î· */
  }

  .projects-card {
    width: 100%;
    max-width: 340px;
  }
}